import {
  getArticle,
} from '@/api/article'
export default {
  middleware: 'authenticated',
  name: 'UserProfile',
  async asyncData ({params}) {
    console.log(params)
    const { data } = await getArticle(params.slug)
    console.log(data)
    return {
      article: data.article
    }
  }
}
